/*
 * For modern browsers
 * 1. The space content is one way to avoid an Opera bug when the
 *  contenteditable attribute is included anywhere else in the document.
 *  Otherwise it causes space to appear at the top and bottom of elements
 *  that are clearfixed.
 * 2. The use of `table` rather than `block` is only necessary if using
 * `:before` to contain the top-margins of child elements.
 */
.cf:before,
.cf:after {
  content: " ";
  /* 1 */
  display: table;
  /* 2 */ }

.cf:after {
  clear: both; }

/*
  * For IE 6/7 only
  * Include this rule to trigger hasLayout and contain floats.
  */
.cf {
  zoom: 1; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.intro {
  background-color: #494949; }

.name-container {
  position: absolute; }

.card_container {
  background-color: white;
  box-shadow: -1px 2px 7px 0 rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  padding: 5px; }

.cloud_card {
  box-shadow: none; }
  .cloud_card .card_header {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; }
    .cloud_card .card_header > :nth-child(n) {
      flex-basis: auto;
      /* default value */ }
    .cloud_card .card_header h2, .cloud_card .card_header p {
      margin-top: 0px; }
    .cloud_card .card_header .card_header__rightside {
      display: flex;
      justify-content: space-evenly; }

.page_projects {
  background-color: #EEEEEE; }

.page_jobs {
  background-color: #71d7ff; }

@-webkit-keyframes roll-in-left {
  0% {
    -webkit-transform: translateX(-800px) rotate(-540deg);
            transform: translateX(-800px) rotate(-540deg);
    opacity: 0; }
  100% {
    -webkit-transform: translateX(0) rotate(0deg);
            transform: translateX(0) rotate(0deg);
    opacity: 1; } }

@keyframes roll-in-left {
  0% {
    -webkit-transform: translateX(-800px) rotate(-540deg);
            transform: translateX(-800px) rotate(-540deg);
    opacity: 0; }
  100% {
    -webkit-transform: translateX(0) rotate(0deg);
            transform: translateX(0) rotate(0deg);
    opacity: 1; } }

.page_wip {
  background-color: #71d7ff;
  display: flex;
  justify-content: center;
  align-items: center; }

.wip-desc {
  color: white;
  text-align: center; }
  .wip-desc > span {
    display: flex;
    justify-content: space-evenly; }
    .wip-desc > span > a {
      color: white; }

.wip-image-container {
  background-color: white;
  width: 300px;
  height: 300px;
  border-radius: 100%;
  box-shadow: 3px 3px 7px 0px rgba(0, 0, 0, 0.185);
  -webkit-animation: roll-in-left 0.6s ease-out both;
          animation: roll-in-left 0.6s ease-out both;
  display: flex; }
  @media only screen and (max-width: 400px) {
    .wip-image-container {
      padding: 20px; } }
  .wip-image-container > img {
    width: inherit;
    align-self: center; }

